import React, { useState } from 'react';
import { Card, Row, Col, Button, Dropdown } from 'react-bootstrap';
import { Line } from 'react-chartjs-2';
import jsPDF from 'jspdf';
import 'jspdf-autotable'; // For table export
import Layout from '@/components/Layout';
import styles from '../styles/Dashboard.module.css';

// Import necessary Chart.js components
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  LineElement,
  PointElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';

// Register Chart.js components
ChartJS.register(CategoryScale, LinearScale, LineElement, PointElement, Title, Tooltip, Legend);

const Dashboard = () => {
  const [selectedMonth, setSelectedMonth] = useState('January');

  // Sales Report Chart Data (Dynamic based on selectedMonth)
  const salesChartData = {
    labels: ['Week 1', 'Week 2', 'Week 3', 'Week 4'], // X-axis labels
    datasets: [
      {
        label: `Sales Report - ${selectedMonth}`,
        data: [1000, 1200, 800, 1500], // Mock data
        borderColor: '#4CAF50',
        backgroundColor: 'rgba(76, 175, 80, 0.2)',
        tension: 0.4,
        pointRadius: 5,
        pointBackgroundColor: '#4CAF50',
      },
    ],
  };

  // Chart Options
  const chartOptions = {
    responsive: true,
    plugins: {
      legend: { display: true, position: 'top' },
      tooltip: { enabled: true },
    },
    scales: {
      x: { grid: { display: false } },
      y: {
        grid: { color: '#e0e0e0' },
        ticks: { callback: (value) => `$${value}` },
      },
    },
  };

  // Export to PDF
  const exportToPDF = () => {
    const doc = new jsPDF();
    doc.text('Sales Report', 10, 10);
    doc.autoTable({
      head: [['Month', 'Week 1', 'Week 2', 'Week 3', 'Week 4']],
      body: [
        [selectedMonth, '1000', '1200', '800', '1500'], // Mock Data
      ],
    });
    doc.save('sales_report.pdf');
  };

  return (
    <Layout>
      <div className={styles.dashboard}>
        <Row className="mt-4">
          <Col xs={12}>
            <Card className={styles.chartCard}>
              <Card.Body>
                {/* Header Section */}
                <div className="d-flex justify-content-between align-items-center">
                  <h5 className={styles.cardTitle}>Sales Report</h5>
                  <div>
                    {/* Month Filter */}
                    <Dropdown>
                      <Dropdown.Toggle variant="success">
                        {selectedMonth}
                      </Dropdown.Toggle>
                      <Dropdown.Menu>
                        {['January', 'February', 'March', 'April', 'May'].map((month) => (
                          <Dropdown.Item
                            key={month}
                            onClick={() => setSelectedMonth(month)}
                          >
                            {month}
                          </Dropdown.Item>
                        ))}
                      </Dropdown.Menu>
                    </Dropdown>

                    {/* Export to PDF */}
                    <Button
                      variant="danger"
                      className="ms-3"
                      onClick={exportToPDF}
                    >
                      Export PDF
                    </Button>
                  </div>
                </div>

                {/* Line Chart */}
                <Line data={salesChartData} options={chartOptions} />
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </div>
    </Layout>
  );
};

export default Dashboard;
